<script>
import {VueRecaptcha} from 'vue-recaptcha';
import VueMask from 'v-mask'
import Vue from "vue";

export default {
  components: {VueRecaptcha},
  name: "FAQ",
  beforeCreate() {
    Vue.use(VueMask);
  },
  data() {
    return {
      data: {
        captureButton: false,
        name: '',
        phone: '',
        message: '',
        email: ''
      },
      categories: [],
      currentLocale: this.$i18n.locale,
    };
  },
  methods: {
    enableButton(){
      window.captureButton=true
    },
    send() {
      if(!window.captureButton){
        Vue.toastr({
          description: 'Подтвердите что вы не робот',
          type: 'error'
        })
        return
      }
      function validateEmail(email) {
        var pattern = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(String(email).toLowerCase());
      }

      if (!validateEmail(this.data.email)) {
        Vue.toastr({
          description: 'Неверно указан имейл',
          type: 'error'
        })
        return
      }

      this.$http.post(`${API_ROOT}/api/feedback`, this.data)
        .then(({data}) => {
          this.$bvModal.hide('feedback')
          this.data = {
            name: '',
            phone: '',
            message: '',
            email: ''
          }
        })
        .catch(err => {
          console.error(err);
        })
    },
    getIconUrl(path) {
      return API_ROOT + path;
    }
  },
  mounted() {
    this.$http.get(`${API_ROOT}/api/fag/categories`)
      .then(({data}) => {
        this.categories = data;
      })
      .catch(err => {
        console.error(err);
      })
  }
}
</script>

